import './Stack.scss';

const Stack = ()=> {
  return(
    <div id="stackOutbox">
      <div className='stackInbox'>
        <div id="devTool">
          <div className="imgGroup">
            <div className="html">
              <img src={ require('../img/1HTML.png') } alt="html logo"/>
              <p>HTML</p>
            </div>
            <div className="css">
              <img src={ require('../img/2CSS.png') } alt="css logo"/>
              <p>CSS</p>
            </div>
            <div className="js">
              <img src={ require('../img/3JS.png') } alt="js logo"/>
              <p>JAVASCRIPT</p>
            </div>
            <div className="react">
              <img src={ require('../img/4react.png') } alt="react logo"/>
              <p>REACT</p>
            </div>
          </div>
          <div className="devText">
            <p><span className="dot-4px"></span>HTML : 기본적인 구조를 잘 파악하고 작성할 수 있습니다.</p>
            <p><span className="dot-4px"></span>CSS : 여러 속성을 통해 애니메이션이나 정적인 페이지를 작성할 수 있습니다.</p>
            <p><span className="dot-4px"></span>JavaScript : 하드코딩은 미숙하나 기본적인 코딩을 작성하고 해석할 수 있습니다.</p>
            <p><span className="dot-4px"></span>React : SPA에 대해 관심있어 리엑트의 동작원리를 이해하고 기본적인 틀을 작성할 수 있습니다.</p>
          </div>
        </div> {/* devTool end */}

        <div id="desiTool">
          <div className="imgGroup">
            <div className="figma">
              <img src={ require('../img/5figma.png') } alt="figma logo"/>
              <p>FIGMA</p>
            </div>
            <div className="il">
              <img src={ require('../img/6il.png') } alt="ilustratior logo"/>
              <p>ILUSTRATOR</p>
            </div>
            <div className="photo">
              <img src={ require('../img/7photo.png') } alt="photoshop logo"/>
              <p>PHOTOSHOP</p>
            </div>
            <div className="pre">
              <img src={ require('../img/8pre.png') } alt="premiere pro logo"/>
              <p>PREMIERE PRO</p>
            </div>
          </div>
          <div className="desiText">
            <p><span className="dot-4px"></span>Figma :  프로토타이핑에 대해 이해하고 화면구현, UI디자인을 할 수 있습니다</p>
            <p><span className="dot-4px"></span> Ilustrator : 간단한 벡터 이미지를 작성 할 수 있습니다.</p>
            <p><span className="dot-4px"></span>Photoshop : 편집과 간단한 썸네일 작업, 합성 등을 할 수 있습니다.</p>
            <p><span className="dot-4px"></span>Premeiere Pro : 영상을 편집 및 제작할 수 있습니다.</p>
          </div>
        </div> {/* desiTool end */}
      </div>  {/* inbox end */}
    </div>
  );
}
export default Stack;