import React from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import { Navigation } from 'swiper';
import data from './ImageData.json';
import './Web.scss';
// import './MySlider.scss';
import WebBox from '../component/WebBox';

// swiper style import
import 'swiper/css';
import 'swiper/css/navigation';

export default function Web() {
  return(
    <div className='web fullPage'>
      <Swiper
        className='banner'
        modules={[Navigation]}
        loop={true}
        centeredSlides={true}
        spaceBetween={20}
        slidesPerView={3}
        navigation
        pagination={{ clickable: true }}
        // onSwiper={(swiper) => console.log(swiper)}
        // autoplay={{ delay : 5000, disableOnInteraction : false }}
      >
        {data.data.map((detail, index) => {
          return(
            <SwiperSlide>
              <WebBox>
                # Javascript
              </WebBox>
            </SwiperSlide>  
          );
        })}
      </Swiper>
    </div>
  );
}