import './About.scss';

const About = ()=> {
  return(
    <div className='fullPage'>
      <div id="aboutInbox" className='baseBox'>
        <div className="imgBox">
          <img src="/" alt="안녕하세요 최선을 다하는 윤준현입니다." />
        </div>
        <div className="textBox">
          <h1>꾸준히 <span>준비하는</span></h1>
          <p>
            안녕하세요, 신입 퍼블리셔 윤준현입니다.<br/>
            저는 제 영향력 안에 있는 사람에게 이로운 영향을 미치는 것에 그치지 않고 <br/>
            보다 많은 사람들에게 이로운 영향을 미칠 수 있는 웹을 만드는 것이 목표입니다.
          </p>
          <p>
            사용자 입장에서 고려하고, 어려움을 겪는 사람에게도 쓰기 편한 웹 개발하는 것,<br/>
            그 또한 퍼블리셔와 서비스 제공자로서의 역할로 생각하고 있습니다.<br/>
            범용성과 유저의 가독성 및 편리함을 위해 고민 또 고민해야 한다고 생각합니다
          </p>
          <p>
            저는 퍼블리셔 뿐만 아니라 미래 프론트엔드 개발자가 되고자<br/>
            바빠도 하루에 2~3시간동안 Javascript와 React에 대해 <br/>
            공부하고 연습한 프로젝트를 GitHub에 올리고 있습니다.
          </p>
          <p>
            계획으로는 Typescript와 Vue에 대해 공부하고, 백엔드 서비스도 겸할 수 있는 Node.js를 활용해보고자 합니다.
          </p>
        </div>
      </div>
    </div>
  );
};
export default About;