import './Intro.scss';
import './common.scss';

const First = ()=>{
  return(
    <div className="first">
      <p className='up'>꾸<span className="jun">준</span>하고 &nbsp;
          <span className="ji">지</span>속하는 웹퍼블리셔
      </p>
      <h4>윤지읒</h4>
      <p className='down'>Web Publisher Portfolio</p>
    </div>
  )
}

const Second = ()=>{
  return(
    <div className="second">
        <h4>
          <span>안녕하세요 :)</span><br/>
          저는 윤준현입니다!
        </h4>
    </div>
  )
}


const Intro = ()=>{
  return(
    <div className="IntroText">
      <div className='inner'>
      <First/>
      <Second />
      </div>
    </div>
  );
}
export default Intro;