import styled from "styled-components";

const WebOutbox = styled.div`
  .innerBox{
    box-sizing: border-box;
    background-color: #fff;
    width: 360px;
    height: 410px;
    outline: 1px solid #111;

    .webImg{
      width: 100%;
      height: 55%;
      img {
        display: block; width:100%; height: 100%;
      }
    }
    .webText {
      position: relative;
      width: 100%;
      height: 45%;
      background-color: #555;
      padding: 12px 26px;
      h4 {
        color: #fff; font-size: 16px; font-weight: 500;
      }
      p.websub {
        color: #d9d9d9; font-size: 12px; margin-top: 6px;
      }
      .webTag {
        display: flex; flex-wrap: wrap;
      }
      .btn {
        width: 1.2rem; height: 1.2rem;
        display:block;
        position: absolute;
        bottom:0.9rem ;
        right:1.5rem;
      }
    }
  }

  .tagOutline {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #7CC6CB;
    padding: 3px 12px;
    border-radius: 15px;
    height: 21px;
    margin: 10px 10px 0 0;
    p{
      color: #7CC6CB;
      font-size: 12px;
      line-height: 16px;
    }
  }
`;



const SmallTag = ({children}) => {
  return(
        <div className="tagOutline">
          <p>{children}</p>
        </div> 
  );
}


const WebBox = ({children})=>{
  return(
    <WebOutbox>
      <div className="innerBox">
        <div className="webImg">
          <img src='/' alt="{name}웹 사이트 사진입니다." />
        </div>
        <div className="webText">
          <div className="webTitle">
            <h4>글 제목</h4>
            <p className="websub"> ~ 어떠한 사이트인지 설명</p>
          </div>
          <div className="webTag">
            <SmallTag>{children}</SmallTag>
          </div>
          <img className="btn" src={ require('../img/btn.png') } alt="자세히 보기 버튼"/>
        </div> {/* webText end */}
      </div>{/* innerBox end */}
    </WebOutbox> 
  );
};
export default WebBox;